@charset "UTF-8";
.nav-link {
  padding: 0.25rem; }

.nav-link:before {
  content: "";
  display: block;
  border-top: 2px solid #000102;
  width: 8rem;
  padding-bottom: 0.5rem; }

.nav-link:last-child:after {
  content: "";
  display: block;
  border-bottom: 2px solid #000102;
  width: 8rem;
  padding-top: 0.5rem; }

.nav-link:hover a::before {
  display: inline-block;
  content: "•";
  margin-right: 0.1rem; }

.nav-link:hover a::after {
  display: inline-block;
  content: "•";
  margin-left: 0.1rem; }
